<template>
  <b-container class="bv-example-row">
    <b-row>
      <b-col class="my-5 mx-2">
        <b-card bg-variant="dark" text-variant="white" :title="homeCard.title">
          <b-card-text>{{ homeCard.info }}</b-card-text>
        </b-card>
      </b-col>
      <b-col class="my-5 mx-2" v-for="item in homeCard.content" :key="item.title">
        <b-card border-variant="dark" :title="item.title" :img-src="item.img" img-alt="Image" img-top style="max-width: 20rem;" class="h-100 mb-2">
          <b-card-text>{{ item.info }}</b-card-text>
          <b-button size="sm" variant="dark" href="#/list">更多</b-button>
        </b-card>
      </b-col>
    </b-row>
  </b-container>
</template>
<script>

export default {
  props: {
    homeCard: {
      type: Object,
      default: function () { return {} }
    }
  },
  data () {
    return {}
  },
  methods: {},
  mounted: function () {

  }
}
</script>


