            id: '12345',
            name: '紀念袋'
          },
          {
            id: '123456',
            name: '紀念配件'
          },
          {
            id: '1234567',
            name: '辦公文具'
          },
          {
            id: '12345678',
            name: '線上課程'
          },
          {
            id: '12345679',
            name: '小額捐款'
          }
        ]
      }
    }
  },
  methods: {
  },
  mounted: function () {

  }
}
</script>


