<template>
  <div>
    <!-- navbar -->
    <navBar></navBar>

    <!-- card 4 -->    
    <b-container class="mt-4 ">
      <b-row align-h="center">
        <b-card no-body class="overflow-hidden" style="min-width: 100%;">
          <b-row  no-gutters>
            <b-col md="4" class="p-3">
              <b-card-img src="/static/clothes.jpg" class="rounded-0"></b-card-img>
            </b-col>
            <b-col md="8">
              <b-card-body >
                <b-card-title class="my-3" title-tag="h2" title="經典款大學T">
                </b-card-title >
                <b-row align-h="center">
                  <b-col md="6">
                    <b-form-group id="input-group-3" label="顏色:" label-for="input-3">
                      <b-form-select
                        id="input-3"
                        :options="color"
                        required
                      ></b-form-select>
                    </b-form-group>
                  </b-col>
                  <b-col md="6">
                    <b-form-group id="input-group-3" label="數量:" label-for="input-3">
                      <b-form-select
                        id="input-3"
                        v-model="amount"
                        :options="number"
                        required
                      ></b-form-select>
                    </b-form-group>
                  </b-col>
                </b-row>
                <b-card-text class="my-3">
                  $:{{ showPrice }}
                </b-card-text>
                <b-button variant="outline-dark">刪除</b-button>
              </b-card-body>
            </b-col>
          </b-row>
        </b-card>
      </b-row>

      <p class="text-right my-3">  1  品項</p>
      <p class="text-right my-3"> {{ showPrice }} 總價 </p>

      <b-row align-h="center">
       <button type="button" class="btn btn-outline-dark btn-sm mx-2" href="#/">繼續購物</button>
        <button type="button" class="btn btn-dark btn-sm mx-2">結帳</button>
      </b-row>

    </b-container>


  </div>
</template>

<script>
import navBar from './lib/navBar.vue'

export default {
  components: {
    navBar
  },
  data () {
    return {
      subpage: 1,
      items: [
        {
          text: '首頁',
          href: '#'
        },
        {
          text: '電子產品',
          href: '#'
        },
        {
          text: 'Banana電腦',
          active: true
        }
      ],
      number: [ 1, 2, 3 ],
      color: ['黑色', '白色', '金色'],
      amount: 1
    }
  },
  methods: {
  },
  computed: {
    showPrice () {
      return this.amount * 43999
    }
  }
}
</script>


