<template>
  <div>
    <b-navbar toggleable="lg" type="dark" variant="dark" sticky>
      <b-navbar-brand href="">管理者介面</b-navbar-brand>
      <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>
      <b-collapse id="nav-collapse" is-nav>
        <b-navbar-nav>
          <b-nav-item href="#/Category">分類管理</b-nav-item>
          <b-nav-item href="#/product">商品管理</b-nav-item>
          <b-nav-item href="#">廣告頁面</b-nav-item>
          <b-nav-item href="#/Purchase">銷貨單據</b-nav-item>
          <b-nav-item href="#/Purchase">訂單紀錄</b-nav-item>
        </b-navbar-nav>
        <!-- Right aligned nav items -->
        <b-navbar-nav class="ml-auto">
          <b-nav-item-dropdown right>
            <!-- Using 'button-content' slot -->
            <template v-slot:button-content>
              使用者
            </template>
            <b-dropdown-item href="#">Profile</b-dropdown-item>
            <b-dropdown-item href="#">Sign Out</b-dropdown-item>
          </b-nav-item-dropdown>
        </b-navbar-nav>
      </b-collapse>
    </b-navbar>

    <b-row>
      <b-col>
        <h2 class="m-4">編輯分類</h2>
        <b-row>
          <b-col cols="4">
            <b-card header="大分類" class="m-3">
              <b-list-group flush>
                <b-list-group-item href="#">小分類1</b-list-group-item>
                <b-list-group-item href="#">小分類2</b-list-group-item>
                <b-list-group-item href="#">小分類3</b-list-group-item>
              </b-list-group>
            </b-card>
          </b-col>
          <b-col cols="4">
            <b-card header="文具" class="m-3">
              <b-list-group flush>
                <b-list-group-item href="#">鉛筆</b-list-group-item>
                <b-list-group-item href="#">鋼筆</b-list-group-item>
                <b-list-group-item href="#">尺規</b-list-group-item>
              </b-list-group>
            </b-card>
          </b-col>
          <b-col cols="4">
            <b-card header="課程" class="m-3">
              <b-list-group flush>
                <b-list-group-item href="#">語文</b-list-group-item>
                <b-list-group-item href="#">數理</b-list-group-item>
                <b-list-group-item href="#">藝術</b-list-group-item>
              </b-list-group>
            </b-card>
          </b-col>
        </b-row>
      </b-col>
      <b-col>
        <h2 class="m-4">新增分類</h2>
        <b-form-group
          id="fieldset-1"
          description="分類的中文名子"
          label="中文名子"
          label-for="input-1"
          :invalid-feedback="invalidFeedback"
          :valid-feedback="validFeedback"
          :state="state"
        >
          <b-form-input class="w-50" id="input-1" v-model="cName" :state="state" trim></b-form-input>
        </b-form-group>
        <b-form-group
          id="fieldset-2"
          description="分類的英文路徑"
          label="英文路徑"
          label-for="input-2"
          :invalid-feedback="invalidFeedback"
          :valid-feedback="validFeedback"
          :state="state"
        >
          <b-form-input class="w-50" id="input-2" v-model="cName" :state="state" trim></b-form-input>
        </b-form-group>
        <b-form-group
          id="fieldset-2"
          description="子分類"
          label="子分類"
          label-for="input-2"
          :invalid-feedback="invalidFeedback"
          :valid-feedback="validFeedback"
          :state="state"
        >
          <b-form-select class="w-50" v-model="selected" :options="options"></b-form-select>
        </b-form-group>               
      </b-col>
    </b-row>
  </div>
</template>

<script>
export default {
  data () {
    return {
      options: [
        { value: 1, text: '新分類' }
      ]
    }
  },
  methods: {
  },
  computed: {
  }
}
</script>


