<template>
  <div>
    <!-- navbar -->
    <navBar></navBar>

    <!-- jumbotron 2 -->
    <b-jumbotron header="王小明" bg-variant="secondary" text-variant="white" class="text-center">
      <p class="mt-4">桃園市桃園區同德六街89號4樓</p>
      <p class="mt-4">033579080 ‧ service@hash-tech.com.tw </p>
      <b-button variant="dark">修改</b-button>
    </b-jumbotron>

    <!-- card 3 -->
    <b-container>
      <b-card no-body>
        <b-card-header header-tag="nav">
          <b-nav card-header tabs>
            <b-nav-item :active="subpage==1" @click="subpage=1">全部訂單</b-nav-item>
            <b-nav-item :active="subpage==2" @click="subpage=2">處理中訂單</b-nav-item>
            <b-nav-item :active="subpage==3" @click="subpage=3">可取貨訂單</b-nav-item>
            <b-nav-item :active="subpage==3" @click="subpage=4">已完成訂單</b-nav-item>
          </b-nav>
        </b-card-header>

        <b-card-body class="text-center" v-if="subpage==1">
          <b-table hover :items="order"></b-table>
        </b-card-body>
        <b-card-body class="text-center" v-if="subpage==2">
          <b-table hover :items="order"></b-table>
        </b-card-body>
        <b-card-body class="text-center" v-if="subpage==3">
          <p>目前沒有訂單</p>
        </b-card-body>
        <b-card-body class="text-center" v-if="subpage==4">
          <b-spinner label="Spinning"></b-spinner>
        </b-card-body>
      </b-card>        
    </b-container>

  </div>
</template>

<script>
import navBar from './lib/navBar.vue'

export default {
  components: {
    navBar
  },
  data () {
    return {
      subpage: 1,
      order: [
        {
          Date: '2019/01/01', Price: '1999', Status: '已完成', PS: '附註', _rowVariant: 'warning' },
        {
          Date: '2019/05/06', Price: '385', Status: '已完成', PS: '附註', _rowVariant: 'warning' },
        {
          Date: '2019/07/20', Price: '2598', Status: '已完成', PS: '附註', _rowVariant: 'warning' },
        {
          Date: '2019/10/01', Price: '288', Status: '可取貨', PS: '附註', _rowVariant: 'info' },
        {
          Date: '2019/10/05', Price: '657', Status: '處理中', PS: '附註', _rowVariant: 'danger' }
      ]
    }
  },
  methods: {
  },
  computed: {
  }
}
</script>


